import { getPhaseStatus, pushNew} from "./debriefingUtils"
import {leafStatusList, M2CA_PhaseIE, SC8CP_PhaseIE} from "@meddbriefer/feedback-data"

let allAssessResults = []
let allProblemResults = []



const getEntriesUnderHeader = (headerID,events,level,populate) => {
    let header = mergeHeaderStatuses(headerID,events)
    if (!!header) { 
        let results = [header]
        let itemIDs = []
        let filtered = []
        if (populate){
            let eventItems
            if (level === 2){
                eventItems = events.filter(e => (e.inPhase === headerID && e.type !== "header"))}
            if (level === 1){
                eventItems = events.filter (e => (e.phaseID === header.id && e.type === "header" && e.subtype === "assessmentSubHeader"))
                //need to filter out duplicate subphases because may be some in the original events list
                for (const item of eventItems){
                    if (!itemIDs.includes(item.id)){
                        itemIDs.push(item.id)
                        filtered.push(item)}   
                    }
                eventItems = filtered
                }
            results = results.concat(eventItems)}
       return results}
    else {return false}
 }

//merges header for a phase or subphase that is temporally split
const mergeHeaderStatuses = (headerID, events) => {
    let overallStatus = "unk"
    let headerStatuses = []
    let headerItems = events.filter(e => e.type === "header" && e.id === headerID)
    let header = headerItems[0]    
    for (const item of headerItems){
        //pushNew(item.phaseOrderStatus, headerStatuses)
        pushNew(item.status, headerStatuses)}
    if (headerStatuses.length > 0) {
        if (headerStatuses.length === 1){overallStatus = headerStatuses[0]}
        else {overallStatus = "errors"}  //must be all good or all absent for either of those to hold
    }
    if (!!header) {header.status = overallStatus}
    return header 
}

    // after phaseOrderStatus updated need to add that result to subphases status value
const updateTopHeaders = (summary) => {
    let statuses = ["good"]
    let newStatus
    // eslint-disable-next-line no-unused-vars
    for (const [key, entry] of Object.entries(summary)) {
        if (entry[0].subtype === "assessmentHeader" && entry[0].status === "good") {
            for (const item of entry.slice(1)) {
                if (!!item.phaseOrderStatus){
                    pushNew(item.phaseOrderStatus, statuses)}
            }
            if (statuses.length === 1) { newStatus = statuses[0] }
            else { newStatus = "errors" }
            if (!!newStatus){entry[0].status = newStatus}
        }
    }
}

function translateEvent(event) {
    if (["irrelevant", "unnecessary"].includes(event.status)) {
        return {}}
    else {
        let newEvent = {}
        //regularize some field names across headers and checklist events
        if (event.labelID) {
            newEvent.id = event.labelID}
        else { newEvent.id = event.id }
        if (event.type === "header") {
            newEvent.subtype = event.id}
        if (!!event.expertPhase) {
            newEvent.inPhase = event.expertPhase}

        //now copy over these fields
        ["label", "status", "phaseOrderStatus", "phase", "subPhase", "labelID", "phaseID", "probLabel", "answerCorrect", "answerDetails", "timestamp", "type"].forEach(fld => {
            if (!!event[fld]) {
                newEvent[fld] = event[fld]}
        })

        //regularize status values in the case of good and absent
        //did not regularize status values for errors yet, not sure if
        //the details matter to condition 2
        if (leafStatusList["good"].includes(newEvent.status)) {
            newEvent.status = "good"}
        if (leafStatusList["absent"].includes(newEvent.status)) {
            newEvent.status = "absent"}
        return newEvent
    }
}

    
export const summarizeAnalysis = (finalActions, problems, requiredPhaseAndSubPhases, requiredPhaseNames, constraintsViolated) => {
    allAssessResults = []
    allProblemResults = []
    let hierAssessResults = {}
    let hierProblemResults = {}
    finalActions.forEach(event => {
        if (event.type !== "intervention" && event.type !== "answer"){
            allAssessResults.push(translateEvent(event))}
        else {allProblemResults.push(translateEvent(event))}

    }) 
    problems.forEach(prob => {
        //let intvHeaderIndex = 1
        let entries = allProblemResults.filter(e => (e.probLabel === prob.probLabel && e.type !== "header"))
        let statuses = entries.map(e => e.status)
        let status = getPhaseStatus(statuses)
        let headerObject = {
            "id": prob.probLabel,
            "probLabel": prob.probLabel,
            "status": status,
            "phaseOrderStatus": "good",
            "subtype": "interventionHeader",
            "type": "header"
        //    "numericalID": intvHeaderIndex++/2000,
        //    "displayColor": "green",
        //    "why": "",
        //    "protocol": ""
        };
        if (entries){
            hierProblemResults[prob.probLabel] = [headerObject].concat(entries)}

    })

    let subLevel = requiredPhaseAndSubPhases
    let topLevel = requiredPhaseNames.filter(e => !subLevel.includes(e))
    
    //need to update status in headers for lower level first since those
    //values will percolate upwards

    subLevel.forEach(headerID  => {
        let entries = getEntriesUnderHeader(headerID,allAssessResults,2,true)
        //console.log("header = ", header, "entries = ", entries)
        if (entries){
            hierAssessResults[headerID] = entries}})

    topLevel.forEach(headerID => {
        let entries = getEntriesUnderHeader(headerID,allAssessResults,1,true)
        if (entries){
            hierAssessResults[headerID] = entries}})
        
            
    // store phase level contraint feedback at the header level for condition 2 
    // (Note that for condition 1 it was stored during the analysis at the first
    // event entry for a phase and is prefaced by misOrderedPhase in status so that
    // it alone won't count as an error later for condition 2)
    let entryToModify 
    let origAction
    let section
    let actions = {
        ...hierAssessResults,
        ...hierProblemResults}
    constraintsViolated.forEach(constraint => {
        origAction = finalActions.filter(e => ((!!e.labelID && e.labelID === constraint.arg1.id) || (e.id === constraint.arg1.id)))[0]

        if (origAction.type === "header"){
            section = actions[origAction.labelID]
            entryToModify = section[0]}
        else {
            if (origAction.type === "intervention"){
                section = actions[origAction.probLabel]
            }
            if (!section){
                section = actions[origAction.workingPhase]} 
            entryToModify = section.filter(e => e.id === constraint.arg1.id)[0]
        }
        if (!!entryToModify){
            if (constraint.type === "phase"){
                entryToModify.phaseOrderStatus = "misOrdered"
                if (!entryToModify.arg2){
                    entryToModify.arg2 = [constraint.arg2.id]}
                else {entryToModify.arg2 = entryToModify.arg2.concat([constraint.arg2.id])}}
            else{
                if (!entryToModify.arg2){
                    entryToModify.arg2 = [constraint.arg2.id]}
                else {entryToModify.arg2 = entryToModify.arg2.concat([constraint.arg2.id])}}}
    })
    updateTopHeaders(hierAssessResults)
    let topLevelSummary = summarize_topLevel(hierAssessResults, hierProblemResults)

    console.log("top-level-analysis: ", topLevelSummary)

    return [hierAssessResults, hierProblemResults, topLevelSummary]

}

export let c2FB = {}
const C2_FIELDS = ["id", "feedbackAbsent", "feedbackOutOfOrder", "feedbackErrors"]
let topHier
let level2Hier
let scenarios =
{"M2CA": M2CA_PhaseIE,
"SC8CP": SC8CP_PhaseIE}

function  storeLeafActions (actions) {
    actions.forEach(action => {
        if (action.subActions.length === 0){
        let newAction = {}
        C2_FIELDS.forEach(fldName => {
            if (!!action[fldName]) {
                newAction[fldName] = action[fldName];
            }
        })
        c2FB[action.id] = newAction
}})}

//called during initialization in analyseEvents
//changes with each scenario what data structure should be parsed
export function get2HierLevelIDs (scen){
  topHier = []
  level2Hier = []
  let phaseIE = scenarios[scen.name]
  let top = phaseIE.Action
  for (let action of top.subActions){
      topHier.push(action.id)
      storeLeafActions(action.subActions)
      for (let subaction of action.subActions) {
          let subActions = subaction.subActions
          if (subActions.length !== 0) { 
              level2Hier.push(subaction.id) 
              storeLeafActions(subActions)}
      }}
          
  console.log("c2FB", c2FB)
}



function summarize_topLevel(assess,probs) {
    let c2Input = {}
    let top = []
    let statuses = []
    let status
    let val
    let table = {
        ...assess,
        ...probs}
    let headerObject = {
        "id": "top",
        "probLabel": "top",
        "status": "",
        "phaseOrderStatus": "good",
        "type": "header"}
    top.push(headerObject)
    topHier.forEach(key => {
        val = table[key]
        if (!!val){
            c2Input[key] = val
            pushNew(val[0].status, statuses)
            top.push(val[0])}
    })
    if (statuses.length === 1) { status = statuses[0] }
            else { status = "errors" }
    top[0].status = status
    level2Hier.forEach(key => {
        val = table[key]
        if (!!val){
            c2Input[key] = val}
    })
    c2Input["top"] = top

return c2Input
}