export const getPhaseStatus = (statuses) => {
    //for collecting first unique item statuses and then
    //unique phase statuses
    let itemStatuses = []
    let phaseStatuses = []

    //get a unique list of item status values
    statuses.forEach(status => {
        pushNew(status,itemStatuses)
        /* if (!itemStatuses.includes(status)){
            itemStatuses.push(status)
        } */
    })
    
    //turn the item status values into phase level status values
    let res
    for (const [key, val] of Object.entries(phaseStatusList)) {
        res = val.filter(value => itemStatuses.includes(value))
        //if there is an intersection, save the key (i.e. is unique)
        if (res.length !== 0)
            pushNew(key,phaseStatuses)
            /* if (!phaseStatuses.includes(key)){
                phaseStatuses.push(key)} */
        }

    if (phaseStatuses.length === 1){return phaseStatuses[0]}  // to be absent or good, all entries must be good or absent
    else {return "errors"}  //otherwise they are not all good to that translates to errors
       
    }



