import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { db } from "@meddbriefer/mdb-firebase"
import { getLog , getPublishedScenarioByID } from "@meddbriefer/scenario-data/index"
import AnalyzeDebriefing from '../components/AnalyzeDebriefing'

const Log = () => {

  const { logID } = useParams()
  const [ log, setLog ] = useState(null)
  const [ scenario, setScenario] = useState(null)
  const [loaded, setLoaded] = useState(false)

  useEffect(
    () => {
      getLog(db,logID)
        .then(logData => {
          setLog(logData)
        })
    }, [logID]
  )

  useEffect(
    () => {
      //!!log && getScenarioByName(db, log.scenarioName, "V2")
      !!log && getPublishedScenarioByID(db, log.scenarioID)
        .then(scenData => {
          setScenario(scenData)
          console.log("scenario", scenData)
        })

    }, [log]
  )

  useEffect(
    () => {
      !!scenario && setLoaded(true)
    }, [scenario]
  )

  if (!loaded) {
    return (<h3>Loading</h3>)
  }
  return ( 
      <AnalyzeDebriefing 
        log={logID} 
      /> 
    )


}

export default Log;
