import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { db } from "@meddbriefer/mdb-firebase"
import { analyzeEvents } from '../components/analyzeEvents'
import { getDebriefLog, getLog, verifyDocument, getPublishedScenarioByID} from "@meddbriefer/scenario-data/index"
import DisplayBaseDebriefing from '../components/DisplayBaseDebriefing'



function DisplayDebriefing () {

  const { logID } = useParams()
  const [observerLog, setObserverLog] = useState(undefined)
  const [log, setLog] = useState(undefined)
  const [scenario, setScenario] = useState(undefined)
  const [analyzed, setAnalyzed] = useState(false)
  const [loaded, setLoaded] = useState(false)

  
  
  async function processLog () {
    //console.log("in processLog to display existing analysis")
    if (! await verifyDocument(db, "debriefing-feedback-logs", logID))
          {analyzeEvents(scenario, observerLog) 
          }
    setAnalyzed(true)
    return (true)
  }
  

  //get observer log first

  useEffect(
    () => {
      getLog(db, logID)
        .then(observerLogData => {  
          //console.log("retrieve observerLogData", observerLogData) 
          setObserverLog(observerLogData)    
        })
        .catch(error => alert('error fetching observer log', error))   
    }, [logID]
  )

  useEffect(
    () => {
      !!observerLog && getPublishedScenarioByID(db, observerLog.scenarioID, "V2")
        .then(scenData => {
          console.log("scenario:", scenData)
          setScenario(scenData)
        })

    }, [observerLog]
  )

  useEffect(
    () => {
      !!scenario && processLog()
      //console.log("check on existing analysis")
        // eslint-disable-next-line
        }, [scenario]
      )
      
  useEffect(
      () => {
        !!analyzed && getDebriefLog(db,logID)
          .then(logData => {
            //console.log("retrieve Debriefing result:", logData)
            setLog(logData)  
          })
          .catch(error => alert('error fetching debriefing log', error))
        // eslint-disable-next-line
        }, [analyzed]
      )

  useEffect(
    () => {
      !!log && setLoaded(true) 
    }, [log]
  )

  if (!loaded) {
    return (<h3>Loading</h3>)
  }


  return (
    <DisplayBaseDebriefing
      log={log}
      actions={log.events}
      scenario={scenario}
      context={"review"} />
  )


}

export default DisplayDebriefing;
